:root {
  --light-blue: #00acdc;
  --black: #000;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  display: flex;
}

.header__logo-link {
  width: 40px;
  height: 40px;
  margin: 10px 0;
}

.header__nav-list {
  justify-content: space-between;
  margin: 0;
  padding: 19px 0;
  list-style: none;
  display: flex;
}

.header__nav-link {
  color: var(--black);
  text-transform: uppercase;
  margin: 0 10px;
  padding: 23px 0;
  font-size: 12px;
  line-height: 22px;
  text-decoration: none;
  transition: all .3s ease-in-out;
}

.header__nav-link:hover {
  color: var(--light-blue);
}

.header__nav-item:first-child .header__nav-link {
  margin: 0 10px 0 0;
}

.header__nav-item:last-child .header__nav-link {
  margin: 0 0 0 10px;
}

.is-active {
  color: var(--light-blue);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--light-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  top: 56px;
  left: 0;
}

